import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    edit: true,
    controls:[
      {label:"AAA",type:"Button",left:100,top:100},
      {label:"BBB",type:"Alert",left:200,top:200},
      {label:"CCC",type:"Button",left:300,top:800},
      {label:"DDD",type:"Button",left:400,top:500},
      {label:"EEE",type:"Alert",left:200,top:500},
      {label:"FFF",type:"Button",left:600,top:800}
    ]
  },
  mutations: {
    appendControl (state,payload){
      state.controls.push({label:"FFF",type:payload.controlType,left:payload.left,top:payload.top})
    },
    setText (state,payload){
      state.controls=payload.text
    },
    edit (state,payload){
      state.edit=payload.text
    }
  },
  actions: {
  },
  modules: {
  }
})
