<template lang="pug">
.box
  h1 Vritual Interface
  label#check Edit Mode?
    input(type="checkbox", checked v-model="edit")
</template>

<script>
export default {
  computed: {
    edit: {
    //   get() {
    //     return JSON.stringify(this.$store.state.edit);
    //   },
      set(value) {
        console.log(value);
        this.$store.commit({ type: "edit", text: JSON.parse(value) });
      },
    },
  },
};
</script>

