<template lang="pug">
.container(
    v-on:dragstart="dragstart")
  h3 Tool Bar
  Box(v-for="type in types"
    label="button",
    :type="type",
    draggable="true",
    v-on:dragstart="dragstart"
  )
</template>

<script>
import Box from "./../Control/Box.vue";

export default {
  components: {
    Box: Box,
  },
  data(){
      return{
        types:["Button","Alert"]
        }
  },
  methods: {
    dragstart: function (e) {
      e.dataTransfer.setData('text/plain',e.target.lastElementChild.attributes.getNamedItem("controltype").value)
      console.log(
        e.target.lastElementChild.attributes.getNamedItem("controltype").value
      );
      console.log(e.target.lastElementChild.attributes);
    },
  },
};
</script>

