<template lang="pug">
#app
  TitleBar#title
  .panel
    .display(v-on:drop="drop", v-on:dragover="allowDrop")
      h3 Display
      Box.control(
        v-for="control in $store.state.controls",
        :label="control.label",
        :type="control.type",
        :style="{ left: control.left + 'px', top: control.top + 'px' }")
    ToolBar#tool(:class="[ { hide: !$store.state.edit }]")
  textarea#text(v-show="$store.state.edit" v-model="text")
</template>

<style lang="sass">
$titleHeight: 70px
$padding: 25px
$marginComp: 15px
$borderRadius: 3px

@mixin shadow()
  box-shadow: 0px 0px 8px rgba(black,0.6)

*
  // border: 1px solid #000
  box-sizing: border-box
html,body,#app
  margin: 0
  width: 100%
  height: 100%
  position: relative
  padding: 0
#app
  padding: $padding
  background-color: #e8e8e8
#title
  width: auto
  height: $titleHeight
  border-radius: $borderRadius
  +shadow()
.panel
  position: relative
  height: calc(100% - #{$titleHeight} - 30px)
  display: flex
  justify-content: center
  transition: 1s
  .display
    position: relative
    flex: auto
    background-color: #e0dfc5
    border-radius: $borderRadius
    +shadow()
    h3
      position: absolute
      left: 50%
      transform: translateX(-50%)
      margin-top: 7px
    .box
      position: absolute
  #tool
    width: 150px
    border-radius: $borderRadius
    overflow: hidden
    transition: 1s
    +shadow()
    &.hide
      width: 0
      margin: $marginComp 0
.display,#tool,#title
  margin: $marginComp
#text
  position: fixed
  right: 30px
  bottom: 30px
  width: 500px
  height: 500px

.display
  transition: 1s

.fade-enter-active, .fade-leave-active 
  transition: opacity .5s

.fade-enter, .fade-leave-to 
  opacity: 0
</style>

<script>
import TitleBar from "./views/TitleBar.vue";
import ToolBar from "./views/ToolBar.vue";
import Box from "./Control/Box.vue";

export default {
  components: {
    TitleBar: TitleBar,
    ToolBar: ToolBar,
    Box: Box,
  },
  data() {
    return {};
  },
  methods: {
    // dragStart: function (event) {
    //   event.dataTransfer.setData("Text", event.target.id);
    // },
    // dragging: function (event) {
    //   // document.getElementById("demo").innerHTML =
    //   //   "The p element is being dragged";
    // },
    allowDrop: function (event) {
      event.preventDefault();
    },
    drop: function (event) {
      // console.log(event)
      let controlType=event.dataTransfer.getData('text/plain')
      // console.log(controlType)
      this.$store.commit({type: 'appendControl',left: event.offsetX-50,top:event.offsetY-50,controlType: controlType})
      // event.preventDefault();
      // var data = event.dataTransfer.getData("Text");
      // event.target.appendChild(document.getElementById(data));
      // document.getElementById("demo").innerHTML = "The p element was dropped";
    },
  },
  computed: {
  text: {
    get () {
      return JSON.stringify(this.$store.state.controls)
    },
    set (value) {
      console.log(value)
      this.$store.commit({type:'setText', text:JSON.parse(value)})
    }
  }}
};
</script>